import React from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars } from "@fortawesome/free-solid-svg-icons";
import {Link} from "react-scroll";

const Navbar = () => {
    
    return (
        <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container">

                <a className="navbar-brand" href="#"></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">

                    <FontAwesomeIcon icon={faBars} style={{ color: "#fff" }} />
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                  
                        <ul className="navbar-nav mr-auto">
                            <li className="nav-item active">
                                <Link smooth={true} to="home" className="nav-link" href="">Home <span className="sr-only">(current)</span> </Link>
                            </li>
                            <li className="nav-item">
                                <Link smooth={true} to="aboutme" offset={-110} className="nav-link" href="">about me</Link>
                            </li>
                            <li className="nav-item">
                                <Link smooth={true} to="services" offset={-110} className="nav-link" href="">Services</Link>
                            </li>
                            <li className="nav-item">
                                <Link smooth={true} to="projects" offset={-110} className="nav-link" href="">projects</Link>
                            </li>
                            <li className="nav-item">
                                <Link smooth={true} to="certificate" offset={-110} className="nav-link" href="">Certificates</Link>
                            </li>
                            <li className="nav-item">
                                <Link smooth={true} to="contacts" offset={-110} className="nav-link" href="">contacts</Link>
                            </li>
                        </ul>
                   
                </div>

             
            </div>
        </nav>
    )
}

export default Navbar
